// https://github.com/zeit/next.js/blob/master/examples/with-redux/pages/_app.js
// https://github.com/mui-org/material-ui/blob/master/examples/nextjs/pages/_app.js
// https://github.com/zeit/next.js/tree/canary/examples/with-segment-analytics

import App, { Container } from 'next/app';
import Router from 'next/router';

import React from 'react';
import withReduxStore from '../libs/with-redux-store';
import { Provider } from 'react-redux';
import { MuiThemeProvider } from '@material-ui/core/styles';
import JssProvider from 'react-jss/lib/JssProvider';

import colors from '../libs/styleguide/colors';
import getPageContext from '../libs/get-page-context';
import analytics from '../libs/analytics-helper';

// Track client-side page views with Segment
Router.events.on('routeChangeComplete', url => {
  analytics.trackPage(url);
});

class MyApp extends App {
  constructor() {
    super();
    this.pageContext = getPageContext();
  }

  componentDidMount() {
    // Remove the server-side injected CSS.
    // eslint-disable-next-line no-undef

    const jssStyles = document.querySelector('#jss-server-side');
    if (jssStyles && jssStyles.parentNode) {
      jssStyles.parentNode.removeChild(jssStyles);
    }
  }

  render () {
    const { Component, pageProps, reduxStore } = this.props;
    return (
      <Container>

        <JssProvider
          registry={this.pageContext.sheetsRegistry}
          generateClassName={this.pageContext.generateClassName}
        >
          <MuiThemeProvider
            theme={this.pageContext.theme}
            sheetsManager={this.pageContext.sheetsManager}
          >
            <Provider store={reduxStore}>
              <Component {...pageProps} />
            </Provider>
          </MuiThemeProvider>
        </JssProvider>
        <style jsx global>{`
          body {
            width: auto;
            background-color: ${colors.darkGrey2};
            font-family: Roboto, Helvetica, Arial, sans-serif;
            margin: 0px;
            overflow-x: hidden;
            overflow-y: scroll;
            color: #ffffff;
            scrollbar-color: transparent; /* For firefox*/
            scrollbar-width: auto; /* For firefox*/
          }
          ::-webkit-scrollbar {
            width: 15px;  /* Remove scrollbar space */
            background: transparent;  /* Optional: just make scrollbar invisible */
          }
        `}</style>

      </Container>
    );
  }
}

export default withReduxStore(MyApp);