import _ from 'lodash';

const UPDATE_BET_AMOUNT = 'UPDATE_BET_AMOUNT';

const UPDATE_ROLL_UNDER = 'UPDATE_ROLL_UNDER';

const UPDATE_BET_HISTORY = 'UPDATE_BET_HISTORY';
const ADD_BETS_TO_HISTORY = 'ADD_BETS_TO_HISTORY';

const UPDATE_GAME_GLOBAL = 'UPDATE_GAME_GLOBAL';

const ROLL_DICE = 'ROLL_DICE';
const ROLL_FINISHED = 'ROLL_FINISHED';

const ACTION_UPDATE_AUTO_BET = 'ACTION_UPDATE_AUTO_BET';

export const NAME_SPACE = 'dice';

export const INITIAL_STATE = {
  rollUnder: 50,
  betAmount: 1.0,
  betStatus: {
    isRolling: false,
    betClientId: null,
    result: null,
  },
  betHistory: {
    allBets: [],
    myBets: [],
    leaderboard: [],
  },
  autoBet: false,
  gameGlobal: {},
};

export const reducer = ( state = INITIAL_STATE, action) => {
  const { type, payload = {} } = action;

  switch (type) {
  case ROLL_DICE: {
    const { betStatus: { isRolling, result }, ...rest } = state;
    const { betClientId } = payload;
    if (isRolling === true) {
      return state;
    }
    return {
      betStatus: {
        betClientId,
        isRolling: true,
        result,
      },
      ...rest,
    };
  }
  case ROLL_FINISHED: {
    const { betStatus: { betClientId }, ...rest } = state;
    const { result } = payload;
    return {
      betStatus: {
        result,
        isRolling: false,
        betClientId,
      },
      ...rest,
    };
  }
  case UPDATE_BET_AMOUNT: {
    const { betAmount } = payload;
    return {
      ...state,
      betAmount,
    };
  }
  case UPDATE_ROLL_UNDER: {
    const { rollUnder } = payload;
    return {
      ...state,
      rollUnder,
    };
  }
  case ADD_BETS_TO_HISTORY: {
    const { betReceipts = [] } = payload;
    const allBets = state.betHistory.allBets.slice();

    if (allBets.length === 0) {
      allBets.push(...betReceipts);
    } else {
      const latestBet = allBets[allBets.length - 1];
      for (let i = 0; i < betReceipts.length; i++) {
        const bet = betReceipts[i];
        if (bet.block_time < latestBet.block_time) {
          continue;
        }
        if (bet.trx_id !== latestBet.trx_id) {
          allBets.push(...betReceipts.slice(i));
          break;
        }
      }
    }
    const newState = _.cloneDeep(state);
    newState.betHistory.allBets = allBets;
    return newState;
  }
  case UPDATE_BET_HISTORY: {
    const { allBets, myBets, leaderboard } = payload;
    const newState = _.cloneDeep(state);
    if (allBets) {
      newState.betHistory.allBets = allBets;
    }
    if (myBets) {
      newState.betHistory.myBets = myBets;
    }
    if (leaderboard) {
      newState.betHistory.leaderboard = leaderboard;
    }

    return newState;
  }
  case UPDATE_GAME_GLOBAL: {
    const { gameGlobal } = payload;
    return {
      ...state,
      gameGlobal,
    };
  }
  case ACTION_UPDATE_AUTO_BET: {
    const { autoBet } = payload;
    return {
      ...state,
      autoBet,
    };
  }
  default:
    return state;
  }
};

export const actions = {
  updateBetAmount: betAmount => ({
    type: UPDATE_BET_AMOUNT,
    payload: { betAmount }
  }),
  updateRollUnder: rollUnder => ({
    type: UPDATE_ROLL_UNDER,
    payload: { rollUnder }
  }),
  diceUpdateBetHistory: (allBets, myBets, leaderboard) => ({
    type: UPDATE_BET_HISTORY,
    payload: { allBets, myBets, leaderboard }
  }),
  diceUpdateMyBets: myBets => ({
    type: UPDATE_BET_HISTORY,
    payload: { myBets }
  }),
  diceUpdateAllBets: allBets => ({
    type: UPDATE_BET_HISTORY,
    payload: { allBets }
  }),
  diceUpdateLeaderboard: leaderboard => ({
    type: UPDATE_BET_HISTORY,
    payload: { leaderboard }
  }),
  rollDice: betClientId => ({
    type: ROLL_DICE,
    payload: { betClientId }
  }),
  rollFinished: result => ({
    type: ROLL_FINISHED,
    payload: { result }
  }),
  diceUpdateGameGlobal: gameGlobal => ({
    type: UPDATE_GAME_GLOBAL,
    payload: { gameGlobal },
  }),
  diceUpdateAutoBet: autoBet => ({
    type: ACTION_UPDATE_AUTO_BET,
    payload: { autoBet },
  })
};

export const selectors = {
  getBetAmount: (store = {}) => _.get(store, 'dice.betAmount'),
  getRollUnder: (store = {}) => _.get(store, 'dice.rollUnder'),
  getDiceAllBets: (store = {}) => _.get(store, 'dice.betHistory.allBets'),
  getDiceMyBets: (store = {}) => _.get(store, 'dice.betHistory.myBets'),
  getDiceLeaderboard: (store = {}) => _.get(store, 'dice.betHistory.leaderboard'),
  getDiceBetHistory: (store = {}) => _.get(store, 'dice.betHistory', {}),
  getBetStatus: (store = {}) => _.get(store, 'dice.betStatus', {}),
  getDiceGameGlobal: (store = {}) => _.get(store, 'dice.gameGlobal', {}),
  diceAutoBet: (store ={}) => _.get(store, `${NAME_SPACE}.autoBet`, false),
};
