import ScatterJS from 'scatterjs-core';
import ScatterEOS from 'scatterjs-plugin-eosjs2';
import jsCookie from 'js-cookie';

import fetch from 'node-fetch';
import { JsonRpc, Api as EosApi } from 'eosjs';
import _ from 'lodash';

import { currentBetToBetreceipt } from '../libs/format-helper';
import { clientOnly } from '../libs/decorators';
import config from '../config';

// Don't forget to tell ScatterJS which plugins you are using.
ScatterJS.plugins(new ScatterEOS());

const { network, contract, app } = config;

let instance = null;

class EosClient {

  contract = null;
  account = null;
  initPromise = null;

  /** @type {WebSocket} */
  scatterSocket = null;

  /** @type {ScatterJS.scatter} */
  scatter = null

  /** @type {EosApi} */
  eos = null;

  /** @type {JsonRpc} */
  rpc = null;

  /** @type {boolean} */
  connected = false

  /** @type {boolean} */
  hasScatter = false

  /**
   * @returns {EosClient}
   */
  static getInstance () {
    if (instance === null) {
      instance = new EosClient();
    }
    return instance;
  }

  constructor() {
    this.contract = contract;
    this.account = jsCookie.getJSON('chain.account');
    this.rpc = new JsonRpc(`${network.protocol}://${network.host}:${network.port}`, { fetch });

    const host = '127.0.0.1:50005';

    if (typeof WebSocket !== 'undefined') {
      this.scatterSocket = new WebSocket(`wss://${host}/socket.io/?EIO=3&transport=websocket`);

      this.scatterSocket.onopen = () => {
        console.log('[scatterSocket]', 'onopen');
        this.scatterSocket.send('40/scatter');
      };

      this.scatterSocket.onmessage = msg => {
        console.log('[scatterSocket]', 'onmessage', msg);
        const { isTrusted, data } = msg;
        if (isTrusted && data === '42/scatter,["connected"]') {
          this.hasScatter = true;
        }
      };

      this.scatterSocket.onerror = event => {
        console.log('[scatterSocket]', 'onerror', event);
      };
    }

  }

  async waitInit() {
    if (this.initPromise === null) {
      this.initPromise = this.init();
      return this.initPromise;
    }
    return this.initPromise;
  }

  async init() {
    this.connected = await ScatterJS.scatter.connect(app.name);

    // If the user does not have Scatter or it is Locked or Closed this will return false;
    if (!this.connected) return this;

    this.scatter = ScatterJS.scatter;

    // Now we need to get an identity from the user.
    // We're also going to require an account that is connected to the network we're using.
    const requiredFields = { accounts: [network] };
    await this.scatter.getIdentity(requiredFields);

    // Always use the accounts you got back from Scatter. Never hardcode them even if you are prompting
    // the user for their account name beforehand. They could still give you a different account.
    this.account = this.scatter.identity.accounts.find(x => x.blockchain === 'eos');

    // Store account
    jsCookie.set('chain.account', this.account, { expires: 14 });

    // Get a proxy reference to eosjs which you can use to sign transactions with a user's Scatter.
    this.eos = this.scatter.eos(network, EosApi, { rpc: this.rpc, beta3: true });

    // ----------------------------
    // Now that we have an identity,
    // an EOSIO account, and a reference
    // to an eosjs object we can send a transaction.
    // ----------------------------

    // Never assume the account's permission/authority. Always take it from the returned account.
    this.transactionOptions = {
      authorization: [{
        actor: this.account.name,
        permission: this.account.authority,
      }],
    };

    if (typeof window !== 'undefined') {
      window.ScatterJS = null;
    }
    return this;
  }

  async getResources(accountName) {
    const account = await this.rpc.get_account(accountName);
    const { ram_quota, ram_usage, net_limit, cpu_limit } = account;
    return {
      ram: {
        quota: ram_quota,
        usage: ram_usage,
      },
      cpu: cpu_limit,
      net: net_limit,
    };
  }


  @clientOnly(null)
  async getMyBalance() {
    let account = this.account;

    if (_.isNil(account)) {
      account = jsCookie.getJSON('chain.account');
    }
    if (_.isNil(account)) {
      await this.initPromise;
      account = this.account;
    }
    if (_.isNil(account)) {
      console.error('[eos-client.getMyBalance]','account is nil');
      throw new Error('account is nil. Is scatter installed ?');
    }

    const result = await this.rpc.get_currency_balance('eosio.token', account.name, 'EOS');

    return result[0];
  }

  /**
   * @param {('dice'|'baccarat'|'pk-10')} gameType
   */
  async gameGlobal(gameType) {
    const result = await this.rpc.get_table_rows({
      json: true,
      code: this.contract[gameType],
      scope: this.contract[gameType],
      table: 'global',
      limit: 1,
    });
    return _.get(result, 'rows[0]');
  }

  /**
   * @param {('dice'|'baccarat'|'pk-10')} gameType
   */
  async currentBets(gameType) {
    const result = await this.rpc.get_table_rows({
      json: true,
      code: this.contract[gameType],
      scope: this.contract[gameType],
      table: 'bets',
      limit: 100,
    });
    const bets = _.get(result, 'rows', []);
    return bets.map(bet => currentBetToBetreceipt(bet)).reverse();
  }

  async diceLatestBetreceipts(offset = 5) {
    let { actions } = await this.rpc.history_get_actions(this.contract.dice, -1, -offset);

    let betreceipts = actions.filter(item => _.get(item, 'action_trace.act.name') === 'betreceipt');
    return betreceipts.map(betreceipt =>  {
      const {
        bet_id: betId,
        bet_memo: betMemo,
        random_roll: randomRoll,
        bettor,
        item,
        payout,
        wager,
      } =  _.get(betreceipt, 'action_trace.act.data', {});
      return {
        betId, betMemo, bettor, item, payout, wager, randomRoll
      };
    });
  }

  async baccaratGameResult(gameId) {
    const contract = this.contract.baccarat;
    const rawResult = await this.rpc.get_table_rows({
      json: true,
      code: contract,
      scope: contract,
      table: 'games',
      limit: 1,
      lower_bound: gameId,
      upper_bound: gameId,
    });

    const { result, ...rest } = _.get(rawResult, 'rows[0]', {});
    return Object.assign({}, result, rest);
  }

  async diceBet(quantity, rollUnder, betMemo) {
    await this.waitInit();
    const memo = `action:bet,item:${rollUnder},user_seed:testsetset,bet_memo:${betMemo}`;
    const result = await this.eos.transact({
      actions: [{
        account: 'eosio.token',
        name: 'transfer',
        authorization: this.transactionOptions.authorization,
        data: {
          from: this.account.name,
          to: this.contract.dice,
          quantity,
          memo,
        },
      }]
    }, {
      blocksBehind: 3,
      expireSeconds: 30,
    });

    return result;
  }

  async baccaratBet(quantity, referrer, item) {
    await this.waitInit();
    const gameConfig = (await this.gameGlobal('baccarat')).game_config;
    const roundId = gameConfig.round_id;
    const gameId = gameConfig.game_id;
    const betMemo = 'qwert1234'; // TODO

    const memo = `round_id:${roundId},game_id:${gameId},referrer:${referrer},action:bet,item:${item},bet_memo:${betMemo}`;
    const result = await this.eos.transact({
      actions: [{
        account: 'eosio.token',
        name: 'transfer',
        authorization: this.transactionOptions.authorization,
        data: {
          from: this.account.name,
          to: this.contract.baccarat,
          quantity,
          memo,
        },
      }]
    }, {
      blocksBehind: 3,
      expireSeconds: 30,
    });

    return result;
  }

  /**
   * `cleos transfer inita bjpkten "40.0000 EOS" "game_id:732628,bet_price:20000,bet_cnt:20,action:singlebet,item:1/0/3/1023/0/32/7/1/1/2,bet_memo:123fdsa" -p inita`
   */
  async pk10Bet(quantity, betPrice, betCount, referrer, action, item) {
    await this.waitInit();
    const gameId = _.get(await this.gameGlobal('pk-10'), 'game_config.game_id');
    const betMemo = 'qwert1234'; // TODO
    const memo = `game_id:${gameId},bet_price:${betPrice},bet_cnt:${betCount},referrer:${referrer},action:${action},item:${item},bet_memo:${betMemo}`;

    const result = await this.eos.transact({
      actions: [{
        account: 'eosio.token',
        name: 'transfer',
        authorization: this.transactionOptions.authorization,
        data: {
          from: this.account.name,
          to: this.contract.pk10,
          quantity,
          memo,
        },
      }]
    }, {
      blocksBehind: 3,
      expireSeconds: 30,
    });

    return result;
  }
}

export default EosClient;