import i18next from 'i18next';

import { En, zhTw, zhCn } from '../config/locales';
import cookie from 'js-cookie';

// https://stackoverflow.com/questions/3191664/list-of-all-locales-and-their-short-codes

let hl = cookie.get('hl') || 'zh_tw';
let i18nInstance;

i18next.init({
  lng: hl,
  fallbackLng: 'zh_tw',
  debug: false,
  resources: {
    en: En,
    zh_tw: zhTw,
    zh_cn: zhCn,
  },
  returnObjects: true
}, (error, t) => {
  if (error) return console.log(error);
  i18nInstance = t;
});

export default i18nInstance;