import _ from 'lodash';

// baccaratGameStatus constants for status
const BACCARAT_GAME_STATE_PLACE_BET = 'BACCARAT_GAME_STATE_PLACE_BET';
const BACCARAT_GAME_STATE_STOP_PLACE_BET = 'BACCARAT_GAME_STATE_STOP_PLACE_BET';
const BACCARAT_GAME_STATE_RESULT = 'BACCARAT_GAME_STATE_RESULT';
const BACCARAT_GAME_STATE_UNKNOWN = 'BACCARAT_GAME_STATE_UNKNOWN';

// baccaratGameStatus actions
const BACCARAT_NEW_GAME = 'BACCARAT_NEW_GAME';
const BACCARAT_STOP_PLACE = 'BACCARAT_STOP_PLACE';
const BACCARAT_FINISH_GAME = 'BACCARAT_FINISH_GAME';
const BACCARAT_SELECT_TOKEN = 'BACCARAT_SELECT_TOKEN';
const BACCARAT_UPDATE_CURRENT_BETS = 'BACCARAT_UPDATE_CURRENT_BETS';
const BACCARAT_CLOSE_GAME_RESULT = 'BACCARAT_CLOSE_GAME_RESULT';
const BACCARAT_REPLACE_HISTORY = 'BACCARAT_REPLACE_HISTORY';
const BACCARAT_UPDATE_BET_HISTORY = 'BACCARAT_UPDATE_BET_HISTORY';

export const NAME_SPACE = 'baccarat';

export const INITIAL_STATE = {
  gameResult: null,
  gameState: BACCARAT_GAME_STATE_UNKNOWN,
  selectedToken: null,
  currentBets: null,
  showGameResult: false,
  history: [],
  betHistory: {
    allBets: [],
    myBets: [],
    leaderboard: [],
  },
  gameGlobal: {},
};

export const reducer = (state = INITIAL_STATE, action) => {
  const { type, payload = {} } = action;

  switch (type) {
  case BACCARAT_NEW_GAME: {
    const { gameGlobal } = payload;
    const newState = _.cloneDeep(state);
    newState.gameGlobal = gameGlobal;
    newState.gameState = BACCARAT_GAME_STATE_PLACE_BET;
    newState.currentBets = [];
    newState.showGameResult = false;
    return newState;
  }
  case BACCARAT_STOP_PLACE: {
    return Object.assign({}, state, { gameState: BACCARAT_GAME_STATE_STOP_PLACE_BET });
  }
  case BACCARAT_FINISH_GAME: {
    const { gameResult } = payload;
    const { history } = state;
    const newHistory = history.slice();
    if ( _.isEqual(_.last(newHistory), gameResult) === false) {
      const { game_id: gameId, round_id: roundId, ...restOfResult } = gameResult;
      newHistory.push({
        gameId,
        roundId,
        result: restOfResult,
      });
    }
    return Object.assign({}, state, {
      gameResult,
      gameState: BACCARAT_GAME_STATE_RESULT,
      showGameResult: true,
      history: newHistory,
    });
  }
  case BACCARAT_SELECT_TOKEN: {
    const { selectedToken } = payload;
    return Object.assign({}, state, { selectedToken });
  }
  case BACCARAT_UPDATE_CURRENT_BETS: {
    const { currentBets } = payload;
    return Object.assign({}, state, { currentBets });
  }
  case BACCARAT_CLOSE_GAME_RESULT: {
    return Object.assign({}, state, {
      showGameResult: false,
    });
  }
  case BACCARAT_REPLACE_HISTORY: {
    const { history } = payload;
    return Object.assign({}, state, { history });
  }
  case BACCARAT_UPDATE_BET_HISTORY: {
    const { allBets, myBets, leaderboard } = payload;
    const newState = _.cloneDeep(state);
    if (allBets) {
      newState.betHistory.allBets = allBets;
    }
    if (myBets) {
      newState.betHistory.myBets = myBets;
    }
    if (leaderboard) {
      newState.betHistory.leaderboard = leaderboard;
    }
    return newState;
  }
  default:
    return state;
  }
};

export const actions = {
  baccaratNewGame: gameGlobal => ({
    type: BACCARAT_NEW_GAME,
    payload: { gameGlobal },
  }),
  baccaratStopPlace: () => ({
    type: BACCARAT_STOP_PLACE,
    payload: {},
  }),
  baccaratFinishGame: gameResult => ({
    type: BACCARAT_FINISH_GAME,
    payload: { gameResult },
  }),
  baccaratSelectToken: selectedToken => ({
    type: BACCARAT_SELECT_TOKEN,
    payload: { selectedToken },
  }),
  baccaratUpdateCurrentBets: currentBets => ({
    type: BACCARAT_UPDATE_CURRENT_BETS,
    payload: { currentBets },
  }),
  baccaratCloseGameResult: () => ({
    type: BACCARAT_CLOSE_GAME_RESULT,
    payload: { open: false },
  }),
  baccaratReplaceHistory: (history) => ({
    type: BACCARAT_REPLACE_HISTORY,
    payload: { history },
  }),
  baccaratUpdateBetHistory: (allBets, myBets, leaderboard) => ({
    type: BACCARAT_UPDATE_BET_HISTORY,
    payload: { allBets, myBets, leaderboard }
  }),
  baccaratUpdateMyBets: myBets => ({
    type: BACCARAT_UPDATE_BET_HISTORY,
    payload: { myBets }
  }),
  baccaratUpdateAllBets: allBets => ({
    type: BACCARAT_UPDATE_BET_HISTORY,
    payload: { allBets }
  }),
  baccaratUpdateLeaderboard: leaderboard => ({
    type: BACCARAT_UPDATE_BET_HISTORY,
    payload: { leaderboard }
  }),
};

export const selectors = {
  getBaccaratGameStatus: (store = {} ) => _.get(store, `${NAME_SPACE}`),
  getBaccaratGameResult: (store = {} ) => _.get(store, `${NAME_SPACE}.gameResult`),
  getBaccaratGameConfig: (store = {} ) => _.get(store, `${NAME_SPACE}.gameGlobal.game_config`),
  getBaccaratCurrentBets: (store = {} ) => _.get(store, `${NAME_SPACE}.currentBets`),
  getBaccaratHistory: (store = {} ) => _.get(store, `${NAME_SPACE}.history`),
  getBaccaratMyBets: (store = {} ) => _.get(store, `${NAME_SPACE}.myBets`),
  baccaratMinBet: (store = {} ) => _.get(store, `${NAME_SPACE}.gameGlobal.baccarat.profit.min_bet`),
  baccaratMaxBet: (store = {} ) => _.get(store, `${NAME_SPACE}.gameGlobal.baccarat.profit.max_bet`),
  baccaratSelectedToken: (store = {} ) => _.get(store, `${NAME_SPACE}.selectedToken`),
  baccaratAllBets: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.allBets`),
  baccaratMyBets: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.myBets`),
  baccaratLeaderboard: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.leaderboard`),
  baccaratBetHistory: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory`, {}),
};