export function clientOnly(fallbackReturn) {
  return (target, name, descriptor) => {
    const oldValue = descriptor.value;
    descriptor.value = function() {
      if (process.browser === false) return fallbackReturn;
      return oldValue.apply(this, arguments);
    };
    return descriptor;
  };
}

export function serverOnly(fallbackReturn) {
  return (target, name, descriptor) => {
    const oldValue = descriptor.value;
    descriptor.value = function() {
      if (process.browser) return fallbackReturn;
      return oldValue.apply(this, arguments);
    };
    return descriptor;
  };

}

