import { createStore, combineReducers, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunkMiddleware from 'redux-thunk';
import _ from 'lodash';
import EosClient from '../libs/eos-client';

import * as pk10Redux from './pk10';
import * as diceRedux from './dice';
import * as baccaratRedux from './baccarat';

// my balance
const ACTION_UPDATE_MY_BALANCE = 'ACTION_UPDATE_MY_BALANCE';
const ACTION_UPDATE_ACCOUNT = 'ACTION_UPDATE_ACCOUNT';

const ACTION_UPDATE_MESSAGE_MODAL = 'ACTION_UPDATE_MESSAGE_MODAL';
const ACTION_UPDATE_LOADING_MODAL = 'ACTION_UPDATE_LOADING_MODAL';
const ACTION_UPDATE_ERROR_MODAL = 'ACTION_UPDATE_ERROR_MODAL';

const INITIAL_STATE = {
  myBalance: null,
  account: null,
  modal: {
    error: {
      open: false,
      content: null,
    },
    message: {
      open: false,
      content: null,
    },
    loading: {
      open: false,
      content: null,
    },
  },
  [baccaratRedux.NAME_SPACE]: baccaratRedux.INITIAL_STATE,
  [pk10Redux.NAME_SPACE]: pk10Redux.INITIAL_STATE,
  [diceRedux.NAME_SPACE]: diceRedux.INITIAL_STATE,
};

const rootReducer = combineReducers({
  myBalance: (state = INITIAL_STATE.myBalance, action) => {
    switch (action.type) {
    case ACTION_UPDATE_MY_BALANCE: {
      const { myBalance } = action.payload;
      return myBalance;
    }
    default:
      return state;
    }
  },
  account: (state = INITIAL_STATE.account, action) => {
    const { type, payload } = action;
    switch (type) {
    case ACTION_UPDATE_ACCOUNT: {
      const { account } = payload;
      return account;
    }
    default:
      return state;
    }
  },
  modal: (state = INITIAL_STATE.modal, action) => {
    const { type, payload } = action;
    switch (type) {
    case ACTION_UPDATE_ERROR_MODAL: {
      const { open, content } = payload;
      const { error, ...rest } = state;
      return { ...rest, error: { open,content } };
    }
    case ACTION_UPDATE_MESSAGE_MODAL: {
      const { open, content } = payload;
      const { message, ...rest } = state;
      return { ...rest, message: { open,content } };
    }
    case ACTION_UPDATE_LOADING_MODAL: {
      const { open, content } = payload;
      const { loading, ...rest } = state;
      return { ...rest, loading: { open,content } };
    }
    default:
      return state;
    }

  },
  [baccaratRedux.NAME_SPACE]: baccaratRedux.reducer,
  [pk10Redux.NAME_SPACE]: pk10Redux.reducer,
  [diceRedux.NAME_SPACE]: diceRedux.reducer,
});

export const store = createStore(rootReducer, INITIAL_STATE);

export function initializeStore(initialState = INITIAL_STATE) {
  return createStore(rootReducer, initialState, composeWithDevTools(applyMiddleware(thunkMiddleware)));
}

export const actions = {
  myBalanceUpdate: myBalance => ({
    type: ACTION_UPDATE_MY_BALANCE,
    payload: { myBalance },
  }),
  // TODO https://github.com/jarvisdotai/rng-webapp/issues/14
  myBalanceUpdateFromRemote: async () => {
    const eosClient = EosClient.getInstance();
    try {
      const myBalance = await eosClient.getMyBalance();
      return {
        type: ACTION_UPDATE_MY_BALANCE,
        payload: { myBalance },
      };
    } catch (error) {
      console.error(error);
    }
  },
  updateAccount: account => ({
    type: ACTION_UPDATE_ACCOUNT,
    payload: { account },
  }),
  logOut: () => ({
    type: ACTION_UPDATE_ACCOUNT,
    payload: { account: null },
  }),
  modal: {
    showError: content => ({
      type: ACTION_UPDATE_ERROR_MODAL ,
      payload: { open: true, content }
    }),
    showMessage: content => ({
      type: ACTION_UPDATE_MESSAGE_MODAL ,
      payload: { open: true, content }
    }),
    showLoading: content => ({
      type: ACTION_UPDATE_LOADING_MODAL ,
      payload: { open: true, content }
    }),
    closeError: () => ({
      type: ACTION_UPDATE_ERROR_MODAL ,
      payload: { open : false }
    }),
    closeMessage: () => ({
      type: ACTION_UPDATE_MESSAGE_MODAL ,
      payload: { open : false }
    }),
    closeLoading: () => ({
      type: ACTION_UPDATE_LOADING_MODAL ,
      payload: { open : false }
    }),
  },
  ...baccaratRedux.actions,
  ...pk10Redux.actions,
  ...diceRedux.actions,
};

export const selectors = {
  getMyBalance: (store = {} ) => _.get(store, 'myBalance'),
  account: ( store = {} ) => _.get(store, 'account'),
  modal: ( store = {} ) => _.get(store, 'modal', {}),
  modalError: ( store = {} ) => _.get(store, 'modal.error', {}),
  modalMessage: ( store = {} ) => _.get(store, 'modal.message', {}),
  modalLoading: ( store = {} ) => _.get(store, 'modal.loading', {}),
  ...baccaratRedux.selectors,
  ...pk10Redux.selectors,
  ...diceRedux.selectors,
};