import t from '../libs/i18n-util';
import _ from 'lodash';

/**
 * cleos get table bjpkten bjpkten bets
 * {
 *   "rows": [{
 *       "meta": {
 *         "game_id": 732628,
 *         "bet_id": 1,
 *         "bet_time": "2019-04-25T09:10:50",
 *         "bettor": "inita",
 *         "wager": "40.0000 EOS",
 *         "act": "singlebet",
 *         "bet_memo": "123fdsa"
 *       },
 *       "body": {
 *         "bet_price": "2.0000 EOS",
 *         "bet_cnt": 20,
 *         "item": [
 *           "1000000000",
 *           "0000000000",
 *           "1100000000",
 *           "1111111111",
 *           "0000000000",
 *           "0000010000",
 *           "1110000000",
 *           "1000000000",
 *           "1000000000",
 *           "0100000000"
 *         ]
 *       }
 *     }
 *   ],
 *   "more": false
 * }
 * {
 *   id: "5ce2875aeec16d51ed12c351",
 *   amtContract: "eosio.token",
 *   betId: "1",
 *   betMemo: "qwert1234",
 *   bettor: "initd",
 *   item: "0010000010,0100000100,0000000000,0000000000,0000000000,0100000100,0000000000,0000000000,0000000000,0100000100",
 *   payout: 19.6,
 *   wager: 16,
 *   symbol: "EOS",
 *   blockTime: "2019-05-20T10:54:18.500Z",
 *   createdAt: "2019-05-20T10:54:18.797Z",
 *   raw: {},
 *   gameId: 733732,
 *   act: "singlebet",
 * }
 */
export function currentBetToBetreceipt(currentBet = {}) {
  const { meta = {}, body = {} } = currentBet;
  const {
    game_id: gameId,
    bet_id: betId,
    bet_time: betTime,
    bettor,
    wager: wagerWithSymbol,
    act,
    bet_memo: betMemo,
  } = meta;
  const {
    bet_price: betPrice,
    bet_cnt: betCnt,
    item,
  } = body;

  let [wager, symbol] = wagerWithSymbol.split(' ');
  wager = parseFloat(wager);

  return {
    item: _.isArray(item)?item.join(','):item,
    isCurrentBet: true,
    raw: currentBet,
    gameId, betId, betTime, bettor, wager, act, betMemo, betPrice, betCnt, symbol,
  };
}

const PK10GameMeta = {
  singleBet: {
    action: 'singlebet',
    text: t('single_bet_1'),
  },
  compoundBet2: {
    action: 'compoundbet2',
    text: t('compound_bet2_1'),
  },
  compoundBet3: {
    action: 'compoundbet3',
    text: t('compound_bet3_1'),
  },
  first2Sum: {
    action: '1st-2nd-sum',
    text: t('first2_sum_1'),
  },
  dragonTiger: {
    action: 'dragon-tiger',
    text: t('dragon_tiger_1'),
  },
  fiveElements: {
    action: '5elements',
    text: t('five_element_1'),
  },
  bigSmallOddEven: {
    action: 'big-small-odd-even',
    text: t('big_small_odd_even_1'),
  },
};

const ElementText = [t('metal'), t('wood'), t('water'), t('fire'), t('earth')];
const BigSmallOddEvenText = [t('big'), t('small'), t('odd'), t('even')];
const DragonTigerText = [t('dragon'), t('tiger')];

const PK10GameMetaActionIndex = {};
Object.entries(PK10GameMeta).map(([key, { action ,text }]) => {
  PK10GameMetaActionIndex[action] = { key, text };
});

/**
 * @typedef pk10ActionItem
 * @type {Object}
 * @property {string} action
 * @property {string} item
 */

/**
 *
 * @param {string} tableName
 * @param {array} selection
 * @returns {pk10ActionItem}
 */
export function encodePK10Bet(tableName, selection) {

  /**
   * @param {array} a
   * @returns {number}
   */
  const arrayEncode = (a) => {
    const bitPresent = a.map(s => s === true ? '1':'0').reverse().join('');
    return parseInt(bitPresent, 2);
  };

  const { action } = PK10GameMeta[tableName];
  let item = null;

  if (_.isNil(action)) {
    throw new Error(`Wrong table name ${tableName}`);
  }

  if (_.isArray(selection[0])) {
    // 2D array
    item = selection.map(s => arrayEncode(s)).join('/');
  } else {
    // 1D array
    item = arrayEncode(selection);
  }

  return { action, item };
}

/**
 *
 * @param {string} action
 * @param {string} item
 * @returns {string}
 */
export function decodePk10Bet(action, item) {
  const { key, text } = PK10GameMetaActionIndex[action];
  switch (key) {
  case 'singleBet':
    // singlebet: 1111111111,1111111111,1111111111,1111111111,1111111111,1111111111,1111111111,1111111111,1111111111,1111111111
    return text + ':' + item
      .split(',')
      .map((s, index) => {
        let combineText = s
          .split('')
          .map((b, index) => (b === '1')?(index+1):null)
          .filter(s => s!==null)
          .join(',');
        return (combineText === '')?null:`${index+1}:${combineText}`;
      })
      .filter(s => s!==null)
      .join(',');

  case 'compoundBet2':
    // compoundbet2: 1111111111,1111111111,0000000000,0000000000,0000000000,0000000000,0000000000,0000000000,0000000000,0000000000
    return text + ':' + item
      .split(',')
      .slice(0, 2)
      .map((s, index) => {
        let combineText = s
          .split('')
          .map((b, index) => (b === '1')?(index+1):null)
          .filter(s => s!==null)
          .join(',');
        return `${index}:${(combineText === '')?'-':combineText}`;
      });
  case 'compoundBet3':
    // compoundbet3: 1111111111,1111111111,1111111111,0000000000,0000000000,0000000000,0000000000,0000000000,0000000000,0000000000
    return text + ':' + item
      .split(',')
      .slice(0, 3)
      .map((s, index) => {
        let combineText = s
          .split('')
          .map((b, index) => (b === '1')?(index+1):null)
          .filter(s => s!==null)
          .join(',');
        return `${index}:${(combineText === '')?'-':combineText}`;
      });
  case 'first2Sum': {
    // 1st-2nd-sum: 111111111111111111111
    return text + ':' + item
      .split('')
      .map((s, index) => (s==='1') ? index + 3: null)
      .filter(s => s!==null)
      .join(',');
  }
  case 'dragonTiger':
    // action:dragon-tiger,item:11,11,11,11,11
    return text + ':' + item.split(',').map(s => {
      let combineText = s.split('').map((b, index) => (b === '1')?DragonTigerText[index]:'').join('');
      return (combineText === '')?'-':combineText;
    });

  case 'fiveElements': {
    // action:5elements,item:11111,11111,11111
    return text + ':' + item.split(',').map(s => {
      let combineText = s.split('').map((b, index) => (b === '1')?ElementText[index]:'').join('');
      return (combineText === '')?'-':combineText;
    });
  }
  case 'bigSmallOddEven': {
    // action:big-small-odd-even,item:1111,1111,1111
    return text + ':' + item.split(',').map(s => {
      let combineText = s.split('').map((b, index) => (b === '1')?BigSmallOddEvenText[index]:'').join('');
      return (combineText === '')?'-':combineText;
    });

  }
  }

}

