export function decodeBaccaratCard(card) {
  if ( _.isNil(card) || !(1 <= card && card <= 52) ) {
    throw new Error(`invalid card ${card}`);
  }
  return {
    rank: (Math.floor((card - 1) / 4) + 1),
    color: ((card - 1) % 4 + 1),
  };
}