import _ from 'lodash';

const PK10_GAME_STATE_UNKNOWN = 'PK10_GAME_STATE_UNKNOWN';

const PK10_ACTION_UPDATE_BET_GAME_TYPE_SINGLE_BET = 'PK10_GAME_TYPE_SINGLE_BET';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET2 = 'PK10_GAME_TYPE_COMPOUND_BET2';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET3 = 'PK10_GAME_TYPE_COMPOUND_BET3';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_FIRST2_SUM = 'PK10_GAME_TYPE_FIRST2_SUM';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_DRAGON_TIGER = 'PK10_GAME_TYPE_DRAGON_TIGER';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_5_ELEMENTS = 'PK10_GAME_TYPE_5_ELEMENTS';
const PK10_ACTION_UPDATE_BET_GAME_TYPE_BIG_SMALL_ODD_EVEN = 'PK10_GAME_TYPE_BIG_SMALL_ODD_EVEN';

const PK10_ACTION_UPDATE_GAME_GLOBAL = 'PK10_ACTION_UPDATE_GAME_GLOBAL';
const PK10_ACTION_UPDATE_GAME_RESULTS = 'PK10_ACTION_UPDATE_GAME_RESULTS';

const PK10_ACTION_CHANGE_CURRENT_TABLE = 'PK10_ACTION_CHANGE_CURRENT_TABLE';

const PK10_ACTION_UPDATE_BET_HISTORY = 'PK10_ACTION_UPDATE_BET_HISTORY';
const PK10_ACTION_UPDATE_CURRENT_BETS = 'PK10_ACTION_UPDATE_CURRENT_BETS';

const PK10_ACTION_CLEAR_SELECTION = 'PK10_ACTION_CLEAR_SELECTION';
const PK10_ACTION_RANDOM_SELECT = 'PK10_ACTION_RANDOM_SELECT';

export const NAME_SPACE = 'pk10';

export const INITIAL_STATE = {
  bets: {
    singleBet: {
      amount: null,
      selection: new Array(10).fill(null).map(()=> new Array(10).fill(false)),
    },
    compoundBet2: {
      amount: null,
      selection: new Array(2).fill(null).map(()=> new Array(10).fill(false)),
    },
    compoundBet3: {
      amount: null,
      selection: new Array(3).fill(null).map(()=> new Array(10).fill(false)),
    },
    first2Sum: {
      amount: null,
      selection: new Array(23).fill(false),
    },
    dragonTiger: {
      amount: null,
      selection: new Array(5).fill(null).map(()=> new Array(2).fill(false)),
    },
    fiveElements: {
      amount: null,
      selection: new Array(3).fill(null).map(()=> new Array(5).fill(false)),
    },
    bigSmallOddEven: {
      amount: null,
      selection: new Array(3).fill(null).map(()=> new Array(4).fill(false)),
    },
  },
  currentTable: 'fiveElements',
  gameGlobal: null,
  gameResults: null,
  gameState: PK10_GAME_STATE_UNKNOWN,
  currentBets: [],
  betHistory: {
    allBets: [],
    myBets: [],
    leaderboard: [],
  },
};

export const TABLE_LIST = Object.keys(INITIAL_STATE.bets);

export const reducer = ( state = INITIAL_STATE, action) => {
  const { type, payload = {} } = action;
  switch (type) {
  case PK10_ACTION_UPDATE_GAME_GLOBAL: {
    const { gameGlobal } = payload;
    return Object.assign({}, state, { gameGlobal });
  }

  case PK10_ACTION_UPDATE_GAME_RESULTS: {
    const { gameResults } = payload;
    return Object.assign({}, state, { gameResults });
  }

  case PK10_ACTION_UPDATE_BET_GAME_TYPE_SINGLE_BET: {
    const { rank, number, value } = payload;
    const newState = _.cloneDeep(state);
    newState.bets.singleBet.selection[rank][number] = value;
    return newState;
  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET2: {
    return state;

  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET3: {
    return state;

  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_FIRST2_SUM: {
    const { selected, value } = payload;
    const newState = _.cloneDeep(state);
    newState.bets.first2Sum.selection[selected] = value;
    return newState;
  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_DRAGON_TIGER: {
    return state;

  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_5_ELEMENTS: {
    const { rank, category, value } = payload;
    const newState = _.cloneDeep(state);
    newState.bets.fiveElements.selection[rank][category] = value;
    return newState;
  }
  case PK10_ACTION_UPDATE_BET_GAME_TYPE_BIG_SMALL_ODD_EVEN: {
    const { rank, category, value } = payload;
    const newState = _.cloneDeep(state);
    newState.bets.bigSmallOddEven.selection[rank][category] = value;
    return newState;
  }
  case PK10_ACTION_CHANGE_CURRENT_TABLE: {
    const { table } = payload;
    const newState = _.cloneDeep(state);
    newState.currentTable = table;
    return newState;
  }
  case PK10_ACTION_UPDATE_BET_HISTORY: {
    const { allBets, myBets, leaderboard } = payload;
    const newState = _.cloneDeep(state);
    if (allBets) {
      newState.betHistory.allBets = allBets;
    }
    if (myBets) {
      newState.betHistory.myBets = myBets;
    }
    if (leaderboard) {
      newState.betHistory.leaderboard = leaderboard;
    }
    return newState;
  }
  case PK10_ACTION_UPDATE_CURRENT_BETS: {
    const { currentBets } = payload;
    return { ...state, currentBets };
  }
  case PK10_ACTION_CLEAR_SELECTION: {
    const { currentTable } = state;
    const newState = _.cloneDeep(state);
    newState.bets[currentTable] = INITIAL_STATE.bets[currentTable];
    return newState;
  }
  case PK10_ACTION_RANDOM_SELECT: {
    const { currentTable } = state;
    const selection = _.cloneDeep(INITIAL_STATE.bets[currentTable].selection);
    for (let i = 0; i < selection.length; i++) {
      const row = selection[i];
      if (_.isArray(row)) {
        for( let j = 0; j < row.length; j++) {
          row[j] = _.random()?true:false;
        }
      } else {
        selection[i] = _.random()?true:false;
      }
    }
    const newState = _.cloneDeep(state);
    newState.bets[currentTable].selection = selection;
    return newState;
  }
  default: {
    return state;
  }
  }
};

export const actions = {
  pk10UpdateGameGlobal: (gameGlobal) => ({
    type: PK10_ACTION_UPDATE_GAME_GLOBAL,
    payload: { gameGlobal },
  }),
  pk10UpdateGameResults: (gameResults) => ({
    type: PK10_ACTION_UPDATE_GAME_RESULTS,
    payload: { gameResults },
  }),
  pk10AddBetSingleBet: (rank, number) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_SINGLE_BET,
    payload: { rank, number, value: true },
  }),
  pk10RemoveBetSingleBet: (rank, number) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_SINGLE_BET,
    payload: { rank, number, value: false },
  }),
  pk10AddBet1st2ndSum: (selected) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_FIRST2_SUM,
    payload: { selected, value: true },
  }),
  pk10RemoveBet1st2ndSum: (selected) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_FIRST2_SUM,
    payload: { selected, value: false },
  }),
  pk10AddBetCompoundBet2: (selection) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET2,
    payload: { selection },
  }),
  pk10AddBetCompoundBet3: (selection) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_COMPOUND_BET3,
    payload: { selection },
  }),
  pk10AddBetDragonTiger: (selection) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_DRAGON_TIGER,
    payload: { selection },
  }),
  pk10AddBetFiveElement: (rank, category) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_5_ELEMENTS,
    payload: { rank, category, value: true },
  }),
  pk10RemoveBetFiveElement: (rank, category) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_5_ELEMENTS,
    payload: { rank, category, value: false },
  }),
  pk10AddBetBigSmallOddEven: (rank, category) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_BIG_SMALL_ODD_EVEN,
    payload: { rank, category, value: true },
  }),
  pk10RemoveBetBigSmallOddEven: (rank, category) => ({
    type: PK10_ACTION_UPDATE_BET_GAME_TYPE_BIG_SMALL_ODD_EVEN,
    payload: { rank, category, value: false },
  }),
  pk10ChangeCurrentTable: (table) => ({
    type: PK10_ACTION_CHANGE_CURRENT_TABLE,
    payload: { table },
  }),
  pk10UpdateBetHistory: (allBets, myBets, leaderboard) => ({
    type: PK10_ACTION_UPDATE_BET_HISTORY,
    payload: { allBets, myBets, leaderboard }
  }),
  pk10UpdateAllBets: allBets => ({
    type: PK10_ACTION_UPDATE_BET_HISTORY,
    payload: { allBets }
  }),
  pk10UpdateMyBets: myBets => ({
    type: PK10_ACTION_UPDATE_BET_HISTORY,
    payload: { myBets }
  }),
  pk10UpdateLeaderboard: leaderboard => ({
    type: PK10_ACTION_UPDATE_BET_HISTORY,
    payload: { leaderboard }
  }),
  pk10UpdateCurrentBets: currentBets => ({
    type: PK10_ACTION_UPDATE_CURRENT_BETS,
    payload: { currentBets },
  }),
  pk10ClearSelection: () => ({
    type: PK10_ACTION_CLEAR_SELECTION,
    payload: {},
  }),
  pk10RandomSelect: () => ({
    type: PK10_ACTION_RANDOM_SELECT,
    payload: {},
  })
};

export const selectors = {
  pk10BetsSingleBet: (store = {}) => _.get(store, `${NAME_SPACE}.bets.singleBet`),
  pk10BetsFirst2Sum: (store = {}) => _.get(store, `${NAME_SPACE}.bets.first2Sum`),
  pk10BetsBigSmallOddEven: (store = {}) => _.get(store, `${NAME_SPACE}.bets.bigSmallOddEven`),
  pk10BetsFiveElement: (store = {}) => _.get(store, `${NAME_SPACE}.bets.fiveElements`),
  pk10CurrentTable: (store = {}) => _.get(store, `${NAME_SPACE}.currentTable`),
  pk10CurrentTableBets: (store = {}) => {
    const table = _.get(store, `${NAME_SPACE}.currentTable`);
    return _.get(store, `${NAME_SPACE}.bets.${table}`);
  },
  pk10GameGlobal: (store = {}) => _.get(store, `${NAME_SPACE}.gameGlobal`),
  pk10GameConfig: (store = {}) => _.get(store, `${NAME_SPACE}.gameGlobal.game_config`),
  pk10GameResults: (store = {}) => _.get(store, `${NAME_SPACE}.gameResults`),
  pk10BetHistory: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory`, {}),
  pk10AllBets: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.allBets`),
  pk10MyBets: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.myBets`),
  pk10Leaderboard: (store = {}) => _.get(store, `${NAME_SPACE}.betHistory.leaderboard`),
  pk10CurrentBets: (store = {}) => _.get(store, `${NAME_SPACE}.currentBets`, {}),
};